.hownd-banner {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 50px 0;
  min-height: 300px;
}
.hownd-banner .acf-innerblocks-container {
  position: relative;
  z-index: 2;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  max-width: 1220px;
}
.hownd-banner .acf-innerblocks-container > *:last-child {
  margin-bottom: 0;
}
.hownd-banner__bg {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.hownd-banner__overlay {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
}
.hownd-banner h1 {
  font-size: 50px;
}
@media screen and (min-width: 768px) {
  .hownd-banner {
    justify-content: center;
    min-height: 475px;
  }
  .hownd-banner h1 {
    font-size: 80px;
  }
}